<template>
  <div class="main-div">
    <div class="logo"></div>
    <div class="description-container">
      <audio controls>
        <source src="@/assets/audio/keelung-to-three.mp3" type="audio/mpeg">
        Your browser does not support the audio element.
      </audio>
      <p style="margin-top: 16px;" class="description">請在發車後，按下「播放鍵」，即開始播放。</p>
      <p class="description">提醒：記得上車！車開了再按播放！</p>
      <br>
      <p class="description-big brood">當你聽完錄音，抵達三坑車站，請按鍵。</p>
      <!-- <p class="description-big">請按鍵</p> -->
    </div>
    <div id="button" @click="nextPage">已抵達</div>
  </div>
</template>

<script>
export default {
  name: 'TrainAudioGuide',
  data () {
    return {
      window: {
        height: window.innerHeight,
        width: window.innerWidth
      }
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
    this.$store.commit('showFooter', false)
  },
  methods: {
    nextPage () {
      this.$router.push({name: 'CommuteSankenStation'})
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    @extend fullScreenImage;
    background-image: url('~@/assets/images/commute/train-station-rail-bg.png');
    min-height: 100vh;
    width: 100%;
    background-color: black;
    text-align: center;
    position: relative;
    .logo{
      @extend centerContainImage;
      margin-top: 10%;
      margin-bottom: 25px;
      display: inline-block;
      width: 120px;
      height: 120px;
      background-image: url('~@/assets/images/go-right-arrow.png');
      visibility: hidden;
    }
    .description {
      @extend transferQuestionGreenText;
      text-align: center;
      margin: 0px 5px;
    }

    .description-big {
      @extend transferQuestionGreenText;
      text-align: center;
      font-size: 1.2em;
    }

    .brood {
      margin-top: 30px;
    }

    #button {
      width: 100px;
      margin: 16px auto 0px;
      border-radius: 30px;
      padding: 10px 18px;
      background-color: #95ff00;
    }
  }
</style>



